<template>
  <p v-if="response && response !== ''">{{response}}</p>
</template>

<script>
import axios from "axios";

export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "Ping",
  data() {
    return {
      response: '',
    }
  },
  mounted() {
    axios.get('https://emyr-portfolio.glitch.me/ping')
        .then(response => {
          console.log(response);
          this.response = response.data;
        })
        .catch(error => {
          console.log(error)
          this.response = error;
        });
  },
}
</script>

